import { POST } from './route';
import { createSupabaseClient } from '@/lib/supabase';
import { cookies } from 'next/headers';

// Mock dependencies
jest.mock('@/lib/supabase');
jest.mock('next/headers', () => ({
  cookies: jest.fn(),
}));

const mockSupabase = {
  from: jest.fn().mockReturnThis(),
  select: jest.fn().mockReturnThis(),
  eq: jest.fn().mockReturnThis(),
  single: jest.fn(),
  insert: jest.fn().mockReturnThis(),
};

const mockCookies = {
  set: jest.fn(),
};

describe('POST /api/auth/guest/email-match', () => {
  beforeEach(() => {
    jest.clearAllMocks();
    (createSupabaseClient as jest.Mock).mockReturnValue(mockSupabase);
    (cookies as jest.Mock).mockResolvedValue(mockCookies);
  });

  describe('Successful authentication', () => {
    it('should authenticate guest with valid email', async () => {
      // Arrange
      const mockGuest = {
        id: 'guest-123',
        email: 'john@example.com',
        group_id: 'group-456',
        first_name: 'John',
        last_name: 'Doe',
        auth_method: 'email_matching',
      };

      const mockSession = {
        id: 'session-789',
        guest_id: 'guest-123',
        token: 'mock-token',
        expires_at: new Date(Date.now() + 24 * 60 * 60 * 1000).toISOString(),
      };

      mockSupabase.single
        .mockResolvedValueOnce({ data: mockGuest, error: null }) // Guest lookup
        .mockResolvedValueOnce({ data: mockSession, error: null }); // Session creation

      mockSupabase.insert.mockResolvedValueOnce({ data: null, error: null }); // Audit log

      const request = new Request('http://localhost:3000/api/auth/guest/email-match', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
          'x-forwarded-for': '192.168.1.1',
          'user-agent': 'Mozilla/5.0',
        },
        body: JSON.stringify({ email: 'john@example.com' }),
      });

      // Act
      const response = await POST(request);
      const data = await response.json();

      // Assert
      expect(response.status).toBe(200);
      expect(data.success).toBe(true);
      expect(data.data).toEqual({
        guestId: 'guest-123',
        groupId: 'group-456',
        firstName: 'John',
        lastName: 'Doe',
      });

      // Verify guest lookup
      expect(mockSupabase.from).toHaveBeenCalledWith('guests');
      expect(mockSupabase.eq).toHaveBeenCalledWith('email', 'john@example.com');
      expect(mockSupabase.eq).toHaveBeenCalledWith('auth_method', 'email_matching');

      // Verify session creation
      expect(mockSupabase.from).toHaveBeenCalledWith('guest_sessions');
      expect(mockSupabase.insert).toHaveBeenCalled();

      // Verify cookie set
      expect(mockCookies.set).toHaveBeenCalledWith(
        'guest_session',
        expect.any(String),
        expect.objectContaining({
          httpOnly: true,
          sameSite: 'lax',
          maxAge: 60 * 60 * 24,
          path: '/',
        })
      );

      // Verify audit log
      expect(mockSupabase.from).toHaveBeenCalledWith('audit_logs');
    });

    it('should normalize email to lowercase', async () => {
      // Arrange
      const mockGuest = {
        id: 'guest-123',
        email: 'john@example.com',
        group_id: 'group-456',
        first_name: 'John',
        last_name: 'Doe',
        auth_method: 'email_matching',
      };

      mockSupabase.single.mockResolvedValueOnce({ data: mockGuest, error: null });
      mockSupabase.single.mockResolvedValueOnce({ data: {}, error: null });
      mockSupabase.insert.mockResolvedValueOnce({ data: null, error: null });

      const request = new Request('http://localhost:3000/api/auth/guest/email-match', {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({ email: 'JOHN@EXAMPLE.COM' }),
      });

      // Act
      await POST(request);

      // Assert
      expect(mockSupabase.eq).toHaveBeenCalledWith('email', 'john@example.com');
    });
  });

  describe('Validation errors', () => {
    it('should return 400 for invalid email format', async () => {
      // Arrange
      const request = new Request('http://localhost:3000/api/auth/guest/email-match', {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({ email: 'invalid-email' }),
      });

      // Act
      const response = await POST(request);
      const data = await response.json();

      // Assert
      expect(response.status).toBe(400);
      expect(data.success).toBe(false);
      expect(data.error.code).toBe('VALIDATION_ERROR');
      expect(data.error.message).toContain('Invalid email format');
    });

    it('should return 400 for missing email', async () => {
      // Arrange
      const request = new Request('http://localhost:3000/api/auth/guest/email-match', {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({}),
      });

      // Act
      const response = await POST(request);
      const data = await response.json();

      // Assert
      expect(response.status).toBe(400);
      expect(data.success).toBe(false);
      expect(data.error.code).toBe('VALIDATION_ERROR');
    });
  });

  describe('Authentication errors', () => {
    it('should return 404 when email not found', async () => {
      // Arrange
      mockSupabase.single.mockResolvedValueOnce({ data: null, error: { message: 'Not found' } });

      const request = new Request('http://localhost:3000/api/auth/guest/email-match', {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({ email: 'notfound@example.com' }),
      });

      // Act
      const response = await POST(request);
      const data = await response.json();

      // Assert
      expect(response.status).toBe(404);
      expect(data.success).toBe(false);
      expect(data.error.code).toBe('NOT_FOUND');
      expect(data.error.message).toContain('Email not found');
    });

    it('should return 404 when guest uses magic_link auth method', async () => {
      // Arrange
      mockSupabase.single.mockResolvedValueOnce({ data: null, error: { message: 'Not found' } });

      const request = new Request('http://localhost:3000/api/auth/guest/email-match', {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({ email: 'magiclink@example.com' }),
      });

      // Act
      const response = await POST(request);
      const data = await response.json();

      // Assert
      expect(response.status).toBe(404);
      expect(data.success).toBe(false);
      expect(data.error.code).toBe('NOT_FOUND');
    });
  });

  describe('Session creation errors', () => {
    it('should return 500 when session creation fails', async () => {
      // Arrange
      const mockGuest = {
        id: 'guest-123',
        email: 'john@example.com',
        group_id: 'group-456',
        first_name: 'John',
        last_name: 'Doe',
        auth_method: 'email_matching',
      };

      mockSupabase.single
        .mockResolvedValueOnce({ data: mockGuest, error: null })
        .mockResolvedValueOnce({ data: null, error: { message: 'Database error' } });

      const request = new Request('http://localhost:3000/api/auth/guest/email-match', {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({ email: 'john@example.com' }),
      });

      // Act
      const response = await POST(request);
      const data = await response.json();

      // Assert
      expect(response.status).toBe(500);
      expect(data.success).toBe(false);
      expect(data.error.code).toBe('SESSION_ERROR');
    });
  });

  describe('Security features', () => {
    it('should sanitize email input', async () => {
      // Arrange
      const mockGuest = {
        id: 'guest-123',
        email: 'john@example.com',
        group_id: 'group-456',
        first_name: 'John',
        last_name: 'Doe',
        auth_method: 'email_matching',
      };

      mockSupabase.single.mockResolvedValueOnce({ data: mockGuest, error: null });
      mockSupabase.single.mockResolvedValueOnce({ data: {}, error: null });
      mockSupabase.insert.mockResolvedValueOnce({ data: null, error: null });

      const request = new Request('http://localhost:3000/api/auth/guest/email-match', {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({ email: '<script>alert("xss")</script>john@example.com' }),
      });

      // Act
      await POST(request);

      // Assert
      // Email should be sanitized (script tags removed) before database query
      // The sanitized email should be 'alertxssjohn@example.com' (tags removed, content remains)
      const eqCalls = mockSupabase.eq.mock.calls;
      const emailCall = eqCalls.find(call => call[0] === 'email');
      expect(emailCall).toBeDefined();
      expect(emailCall![1]).not.toContain('<script>');
      expect(emailCall![1]).not.toContain('</script>');
    });

    it('should set secure cookie in production', async () => {
      // Arrange
      const originalEnv = process.env.NODE_ENV;
      process.env.NODE_ENV = 'production';

      const mockGuest = {
        id: 'guest-123',
        email: 'john@example.com',
        group_id: 'group-456',
        first_name: 'John',
        last_name: 'Doe',
        auth_method: 'email_matching',
      };

      mockSupabase.single.mockResolvedValueOnce({ data: mockGuest, error: null });
      mockSupabase.single.mockResolvedValueOnce({ data: {}, error: null });
      mockSupabase.insert.mockResolvedValueOnce({ data: null, error: null });

      const request = new Request('http://localhost:3000/api/auth/guest/email-match', {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({ email: 'john@example.com' }),
      });

      // Act
      await POST(request);

      // Assert
      expect(mockCookies.set).toHaveBeenCalledWith(
        'guest_session',
        expect.any(String),
        expect.objectContaining({
          secure: true,
        })
      );

      // Cleanup
      process.env.NODE_ENV = originalEnv;
    });

    it('should log IP address and user agent', async () => {
      // Arrange
      const mockGuest = {
        id: 'guest-123',
        email: 'john@example.com',
        group_id: 'group-456',
        first_name: 'John',
        last_name: 'Doe',
        auth_method: 'email_matching',
      };

      mockSupabase.single.mockResolvedValueOnce({ data: mockGuest, error: null});
      mockSupabase.single.mockResolvedValueOnce({ data: {}, error: null });
      mockSupabase.insert.mockResolvedValueOnce({ data: null, error: null });

      const request = new Request('http://localhost:3000/api/auth/guest/email-match', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
          'x-forwarded-for': '192.168.1.100',
          'user-agent': 'Test Browser',
        },
        body: JSON.stringify({ email: 'john@example.com' }),
      });

      // Act
      await POST(request);

      // Assert - Session insert should include IP and user agent
      const insertCalls = mockSupabase.insert.mock.calls;
      expect(insertCalls.length).toBeGreaterThan(0);
      
      // Find the guest_sessions insert call
      let sessionInsertData = null;
      for (let i = 0; i < insertCalls.length; i++) {
        // Check if this insert is for guest_sessions by looking at the from() call before it
        const fromCallIndex = mockSupabase.from.mock.calls.findIndex((call, idx) => 
          call[0] === 'guest_sessions' && idx < mockSupabase.insert.mock.invocationCallOrder[i]
        );
        if (fromCallIndex !== -1) {
          sessionInsertData = insertCalls[i][0];
          break;
        }
      }
      
      expect(sessionInsertData).toBeDefined();
      expect(sessionInsertData).toHaveProperty('ip_address');
      expect(sessionInsertData).toHaveProperty('user_agent');
    });
  });
});
